* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.container {
  background-color: #2a3132;
  color: white;
  text-align: center;
  width: 100vw;
  min-height: 100vh;
  padding-bottom: 20px;
}

.container h1 {
  font-size: 50px;
  padding: 20vh 0 10vh 0;
  margin: 0;
  color: #90afc5;
}

.todo-list {
  max-width: 300px;
  background-color: #336b87;
  border: 1px solid black;
  border-bottom: none;
  margin: auto;
  -webkit-box-shadow: 0px 0px 25px 9px rgba(0, 0, 0, 0.57);
          box-shadow: 0px 0px 25px 9px rgba(0, 0, 0, 0.57);
}

.form {
  width: 100%;
  border-bottom: 1px solid black;
}

.input {
  width: 85%;
  padding: 10px;
  border: none;
}

input:focus-visible {
  outline: -webkit-focus-ring-color auto 0;
}

.button {
  width: 15%;
  padding: 5px;
  border-width: 0;
  border-left: 1px solid black;
  background-color: #336b87;
  color: white;
  cursor: pointer;
}

.button span {
  font-size: 32px;
}

.divtask {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 5px;
  border-bottom: 1px solid black;
}

.span {
  padding-right: 10px;
  width: 85%;
  padding: 5px;
}
